"use client";
import { useState, useEffect } from "react";
import { useRouter } from "next/navigation";
import { useLanguage } from "@/components/LanguageProvider";
import { requireAdminAuth } from "@/lib/adminAuth";
import {
  getAllAdmissionExams,
  admissionExamsAPI,
} from "@/lib/admissionExamsApi";
import DashboardHeader from "@/components/admin/dashboard/DashboardHeader";
import DashboardSidebar from "@/components/admin/dashboard/DashboardSidebar";
import AdmissionExamsPageHeader from "@/components/admin/admissionExams/AdmissionExamsPageHeader";
import AdmissionExamsTable from "@/components/admin/admissionExams/AdmissionExamsTable";
import BackToDashboardButton from "@/components/admin/admissionExams/BackToDashboardButton";

const AdmissionExamsPage = () => {
  const router = useRouter();
  const { t, lang } = useLanguage();
  const [exams, setExams] = useState([]);
  const [isLoading, setIsLoading] = useState(true);
  const [error, setError] = useState(null);

  useEffect(() => {
    requireAdminAuth();
    loadExams();
  }, []);

  // Reload exams when returning to the page
  useEffect(() => {
    const handleFocus = () => {
      loadExams();
    };

    window.addEventListener("focus", handleFocus);
    return () => window.removeEventListener("focus", handleFocus);
  }, []);

  const loadExams = async () => {
    try {
      setIsLoading(true);
      setError(null);
      const data = await getAllAdmissionExams();
      setExams(Array.isArray(data) ? data : []);
    } catch (err) {
      console.error("Error loading admission exams:", err);
      setError(err.message);
    } finally {
      setIsLoading(false);
    }
  };

  const handleEditExam = (examId) => {
    router.push(`/admin/admissionExams/edit/${examId}`);
  };

  const handleDeleteExam = async (examId) => {
    if (!confirm(t("admissionExams.deleteConfirm"))) {
      return;
    }

    try {
      await admissionExamsAPI.deleteExam(examId);
      await loadExams();
    } catch (err) {
      console.error("Error deleting admission exam:", err);
      alert(t("admissionExams.deleteError"));
    }
  };

  if (isLoading) {
    return (
      <div
        className={`min-h-screen bg-gray-50 ${lang === "ar" ? "rtl" : "ltr"}`}
      >
        <DashboardHeader />
        <div className="flex main-layout">
          <div className={lang === "ar" ? "order-2" : "order-1"}>
            <DashboardSidebar />
          </div>
          <div className={`flex-1 ${lang === "ar" ? "order-1" : "order-2"}`}>
            <div className="p-6">
              <div className="text-center py-12">
                <div className="animate-spin rounded-full h-12 w-12 border-b-2 border-indigo-600 mx-auto"></div>
                <p className="mt-4 text-gray-600">
                  {t("admissionExams.loading")}
                </p>
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }

  return (
    <div className={`min-h-screen bg-gray-50 ${lang === "ar" ? "rtl" : "ltr"}`}>
      <DashboardHeader />
      <div className="flex main-layout">
        <div className={lang === "ar" ? "order-2" : "order-1"}>
          <DashboardSidebar />
        </div>
        <div className={`flex-1 ${lang === "ar" ? "order-1" : "order-2"}`}>
          <div className="p-6">
            <AdmissionExamsPageHeader />

            {error && (
              <div className="mb-6 p-4 bg-red-50 border border-red-200 rounded-md">
                <p className="text-red-600">{error}</p>
              </div>
            )}

            <AdmissionExamsTable
              exams={exams}
              onEditExam={handleEditExam}
              onDeleteExam={handleDeleteExam}
            />

            <BackToDashboardButton />
          </div>
        </div>
      </div>
    </div>
  );
};

export default AdmissionExamsPage;
